.container-proyectos {
  padding: 3rem 0;
  @media (min-width: 991px) {
    padding: 6rem 0;
  }
  .col-lg-12 {
    img {
      top: 7%;
      @media (min-width: 991px) {
        top: 10%;
      }
    }
    h2 {
      font-size: 4rem;
      padding: 1rem 0;
      @media (min-width: 991px) {
        font-size: 8rem;
      }
    }
    p {
      font-size: 1.5rem;
    }
  }
  .col-lg-6 {
    h3 {
      font-size: 4rem;
      padding: 1rem 0;
      @media (min-width: 991px) {
        font-size: 5rem;
      }
    }
    p {
      font-size: 1.5rem;
    }
  }
}
